import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Pegawai } from 'src/app/sdk/hr/models/pegawai';
import { Pegawai_bpjs } from 'src/app/sdk/payroll/models/pegawai-_bpjs';
import { Component, OnInit } from '@angular/core';
import { PayrollPegawaiBpjsService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollPegawaiBpjsService: PayrollPegawaiBpjsService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private pegawaiDetailService: PegawaiDetailService
    ) { }

    id_pegawai: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(
            data => {
                this.id_pegawai = data;
                this.getPegawai()
            }
        )
    }

    pegawai: Pegawai = {}
    getPegawai() {
        this.spinner.show()
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
            data => {
                this.pegawai = data.data
                this.spinner.hide();

                this.getList('bpjs_jkk');
                this.getList('bpjs_jkm');
                this.getList('bpjs_jht');
                this.getList('bpjs_jp');
                this.getList('bpjs_kesehatan');

            }
        )
    }


    bpjs: any = {}
    getList(for_fild) {
        this.spinner.show()
        this.payrollPegawaiBpjsService.getPayrollPegawaiBpjs(JSON.stringify({ for_fild: for_fild })).subscribe(
            data => {
                this.bpjs[for_fild] = data.data;
                this.spinner.hide()
                console.log(this.bpjs)
            }
        )
    }

    update(for_fild, id_bpjs_tk) {
        let param: Pegawai_bpjs = {
            id_bpjs_tk: id_bpjs_tk,
            id_pegawai: this.id_pegawai,
            jenis_bpjs: for_fild,
        }
        this.spinner.show();
        this.payrollPegawaiBpjsService.postPayrollPegawaiBpjs(param).subscribe(
            data => {
                this.pesan.pesanBerhasilForm('Data berhasil di update');
                // this.getList()
                this.spinner.hide()
            }
        )
    }

}
