import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PayrollBpjsJenisService, PayrollBpjsTkService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Bpjs_tk } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollBpjsJenisService: PayrollBpjsJenisService,
        private payrollBpjsTkService: PayrollBpjsTkService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    jenis_bpjs: string = '';
    id_bpjs_tk: string = '';
    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('id_bpjs_tk')) {
                    this.id_bpjs_tk = data.get('id_bpjs_tk');
                    this.form();
                    this.getData()
                }
            }
        )
    }

    getData()
    {
        this.payrollBpjsTkService.getPayrollBpjsTksId(this.id_bpjs_tk).subscribe(
            data => {
                this.jenis_bpjs = data.data.jenis_bpjs;

                this.bpjs.setValue(data.data);
            }
        )
    }

    bpjs: FormGroupTypeSafe<Bpjs_tk>;
    form() {
        this.bpjs = this.fb.group<Bpjs_tk>(
            {
                id_bpjs_tk: new FormControl(''),
                id_perusahaan: new FormControl(''),
                nama_kebijakan: new FormControl('', [Validators.required]),
                keterangan_kebijakan: new FormControl(''),
                jenis_bpjs: new FormControl(this.jenis_bpjs, [Validators.required]),

                status: new FormControl('1'),
                pekerja: new FormControl('0'),
                perusahaan: new FormControl('0'),
                dasar_premi: new FormControl('gaji'),
                dasar_premi_value: new FormControl('0'),

                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_user: new FormControl('')
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.bpjs.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.bpjs.invalid) {
            console.log(this.bpjs.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.payrollBpjsTkService.putPayrollBpjsTksId({id: this.id_bpjs_tk, body: this.bpjs.value}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/asuransi/bpjs'])
    }


}
