import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { BpjsRoutingModule } from './bpjs-routing.module';
import { ListComponent } from './list/list.component';
import { NgZorroAntdModule, NzSelectModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
    declarations: [ListComponent],
    imports: [
        CommonModule,
        BpjsRoutingModule,
        NgZorroAntdModule,
        NzSelectModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]

})
export class BpjsModule { }
