import { Bpjs_jenis } from 'src/app/sdk/payroll/models/bpjs-_jenis';
import { Component, OnInit } from '@angular/core';
import { PayrollBpjsJenisService } from 'src/app/sdk/payroll/services/payroll-bpjs-jenis.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PayrollBpjsTkService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollBpjsJenisService: PayrollBpjsJenisService,
        private payrollBpjsTkService: PayrollBpjsTkService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    ngOnInit() {
        this.getList()
    }

    listBpjsJenis: any[] = []
    getList() {
        this.spinner.show()
        this.payrollBpjsJenisService.getPayrollBpjsJenis().subscribe(
            data => {
                this.listBpjsJenis = data.data

                //cari anak anaknyo
                this.listBpjsJenis.forEach((element, index) => {
                    this.payrollBpjsTkService.getPayrollBpjsTks(JSON.stringify({ jenis_bpjs: element.bpjs_jenis })).subscribe(
                        res => {
                            this.listBpjsJenis[index]['item'] = res.data;
                        }
                    )
                });


                this.spinner.hide()
            }
        )
    }

    delete(id) {
        this.payrollBpjsTkService.deletePayrollBpjsTksId(id).subscribe(
            data => {
                this.pesan.pesanBerhasilForm(data.message);
                this.getList();
            }
        )
    }


}
